.daily-check-in {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    max-width: 400px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .daily-check-in h3 {
    margin: 0 0 15px;
    font-size: 1.5rem;
    color: #333;
  }
  
  .daily-check-in p {
    margin: 10px 0;
    font-size: 1rem;
    color: #555;
  }
  
  .daily-check-in input {
    width: calc(100% - 20px);
    padding: 10px;
    font-size: 1rem;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  }
  
  .daily-check-in button {
    display: block;
    width: 100%;
    padding: 10px 15px;
    font-size: 1rem;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .daily-check-in button:hover {
    background-color: #0056b3;
  }
  
  .daily-check-in .streak {
    margin-top: 20px;
    padding: 10px;
    background-color: #e9f7ef;
    border: 1px solid #d4edda;
    color: #155724;
    border-radius: 5px;
    text-align: center;
    font-size: 1.2rem;
  }
  
  .daily-check-in .message {
    margin-top: 10px;
    padding: 10px;
    background-color: #fff3cd;
    border: 1px solid #ffeeba;
    color: #856404;
    border-radius: 5px;
    text-align: center;
    font-size: 1rem;
  }
  