/* Styles specific to BudgetDisplay component */
.budget-display {
  margin: 20px 0;
}

.budget-display h2 {
  font-size: 22px;
  margin-bottom: 15px;
}

.budget-display h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.budget-display .income-section,
.budget-display .expenses-section,
.budget-display .remaining-income-section {
  margin-bottom: 20px;
}

.budget-display p {
  margin: 5px 0;
}

.budget-display .money-positive {
  background-color: #e0ffe0;
  color: #006600;
  padding: 5px;
  border-radius: 4px;
}

.budget-display .money-negative {
  background-color: #ffe0e0;
  color: #cc0000;
  padding: 5px;
  border-radius: 4px;
}

.savingsbreakdown {
  display: flex;
  flex-direction: column;
}

.savingsbreakdown p {
  background-color: #f3f3f3;
color: black;
}

/* Style for the savings amounts */
.savings-amount {
  font-weight: bold;
  background-color: #45a049 ; /* Use the same green for amounts */
  color: white;

  padding: 5px;
  border-radius: 5px;
}

.monthly-savings-section p {
  background-color: #f9f9f9;
  color: black;
  padding: 5px;
  border-radius: 5px;
  display: inline;
  font-weight: bold;
}